

.timeline
{
    position: relative;
    max-width: 100%;
    margin: 100px auto;
}

.date1
{
    position: absolute;
    top: -20px;
    left: 0px;
    width: 400px;
    height: 35px;
    border-radius: 20px;
    color: #fff;
    place-items:  center;
    font-size: 14px;

}
.container2
{
    padding: 10px 65px;
    position: relative;
    width: 50%;
opacity: 0;
animation: moveup 1s linear forwards;

z-index: 10;
}

@keyframes moveup {
    0%
    {
        opacity: 1;
        z-index: 10;
        transform: translateY(30px);
    }
    100%
    {
    opacity: 1;
     z-index: 10;
        transform: translateY(0px);
    }
    

    
}

.container2:nth-child(1)
{
    animation-delay: 1s;
}

.container2:nth-child(2)
{
    animation-delay: 0s;
}

.text-box{
    padding: 20px 20px;
    background:  #fff;
    position: relative;
    border-radius: 6px;
    font-size: 15px;
}

.left-container
{
    left:0;
}

.right-container
{
    left:50%;
}

.container2 img
{
    position: absolute;
    width: 100px;
    border-radius: 50%;
    right: -50px;
    top: 32px;
    z-index: 289;

}

.right-container img
{
    left: -50px;
}

.timeline::after
{
    content:'';
    position: absolute;
    width: 6px;
    height: 100%;
    background: #fff;
    bottom: 0;
    left:50%;
    margin-left: -3px;
    z-index: 1;
    animation:  moveline 2s linear backwards;
}

@keyframes moveline
{
    0%
    {
        height: 0;
    }
    100%
    {
        height: 100%;
    }
}
.text-box h4{
    font-weight: 600;
    font-size: 24px;
}

.text-box small
{
    
    left:50%;
    margin-bottom: 15px ;
}

.text-box p
{
    padding: 3px;
}
.left-container-arrow
{
    height: 0;
    width: 0;
    position: absolute;
    top: 54px;
    z-index: 1;
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
    border-left: 15px solid #fff;
    right: -15px;
}


.right-container-arrow
{
    height: 0;
    width: 0;
    position: absolute;
    top: 54px;
    z-index: 1;
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
    border-right: 15px solid #fff;
    left: -15px;
}

@media screen and (max-width: 900px)
{
    .timeline
    {
        margin: 50px auto;
    }

    .timeline::after
    {
        left: 31px;
    }

    .container2
    {
        width: 100%;
        padding-left: 80px;
        padding-right: 25px;

    }
    .container2 img
{
    position: absolute;
    width: 64px;
    right: -50px;
    top: 32px;
    z-index: 289;

}




    .right-container
    {
        left: 0;
    }

    .left-container img, .right-container img
    {
        left: 0px;
    }

    .left-container-arrow, .right-container-arrow
    {   
        top: 36px;
        border-right: 15px solid #fff;
        border-left: 0;
        left: -15px;

    }
}